import React,{Component} from 'react';
import PropTypes from 'prop-types';
import {Mutation} from 'react-apollo';
import Router from 'next/router';
import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';
import gql from 'graphql-tag';
import styled from 'styled-components';
import ErrorMessage from './ErrorMessage';


const Center = styled.div`
 text-align:center;
`
const CREATE_ITEM_MUTATION = gql`
 mutation CREATE_ITEM_MUTATION(
   $title:String!
   $description:String!
   $price:Int!
   $image:String
   $largeImage:String
 ){
   createItem(
     title:$title
     description:$description
     price:$price
     image:$image
     largeImage:$image
   ){
     id
   }
 }
`
class CreateItem extends Component{
  state = {
    title:"Cool J's",
    description:"Sweet kicks",
    image:"none.png",
    largeImage:"nope.png",
    price:45

  }

  onChange = (e)=>{
    const {name,value,type} = e.target;
    const val = type === "number" ? parseFloat(value) : value;
    this.setState({[name]:val});
  }

  render(){
    return(
     <Mutation mutation={CREATE_ITEM_MUTATION}
      variables={this.state}>
      {(createItem,{loading,error})=>(
        <Form onSubmit={(e)=>{
          e.preventDefault();
          console.log(this.state);
        }}>
        <ErrorMessage error={error} />
         <fieldset disabled={loading} aria-busy={loading}>
          Title
          <label htmlFor="title">
           <input type="text" onChange={this.onChange}
           placeholder="Title" requiredvalue={this.state.title} name="title"
           id="title" />
          </label>
           Price
          <label htmlFor="price">
           <input type="number" onChange={this.onChange}
           placeholder="Price" requiredvalue={this.state.price} name="price"
           id="price" />
          </label>
          Description
         <label htmlFor="description">
           <textarea
           type="text" onChange={this.onChange}
           placeholder="Enter description" requiredvalue={this.state.description} name="description"
           id="description"
           />
         </label>
         <button type="onSubmit">Submit</button>
         </fieldset>
        </Form>

      )}

     </Mutation>

    )
  }
}







export default CreateItem;
export { CREATE_ITEM_MUTATION };
